.banner {
    width: 80%;
    margin: 0 auto;
    text-align: center;
    
}

.name {
    font-size: 25px;
    font-weight: 700;
    line-height: 15px;
    color: white;
    margin-bottom: 0;

}

.title {
    font-size: 13px;
    font-weight: 400;
    line-height: 15px;
    color: #F3BF99;
    margin-bottom: 0;

}

.email {
    font-size: 11px;
    line-height: 15px;
    font-weight: 400;
    color: #F5F5F5;
    margin-top: 8px;
    margin-bottom: 18px;


}

.contact {
    display: flex;
    justify-content: center;
    align-items: center;

}

button {
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 115px;
    height: 34px;
    top: 468px;
    left: 151px;
    padding: 9px 13px 9px 11px;
    border-radius: 6px;
    gap: 8px;
    border-style: none;
    
}
button > img {
    padding : 10px;
    width: 16px;
    height: 16px;
}
button >h6 {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #374151;
    ;
}
.linkedin-but {
    background: #5093E2;
    box-shadow: 0px 1px 2px 0px #0000000D;

}

.email-but {
    background: #D1D5DB;
    box-shadow: 0px 1px 2px 0px #0000000d;
}

